import React from 'react';

import * as styles from './index.module.scss';

const Heading = ({ primary, secondary, color, center, ...props }) => (
  <div className={`d-flex flex-column text-${color} ${styles.heading}`} {...props}>
    <div>
      <h2 className={`fw-bold ${center ? 'text-center' : ''}`}>
        <span>{secondary ? `${secondary} ` : null}</span> {primary}
      </h2>

      <div
        className={`rounded-pill ${center ? 'mx-auto' : ''} bg-${
          color === 'altdarker' ? 'primary' : 'white'
        } ${styles.divider}`}
      />
    </div>
  </div>
);

export default Heading;
