import React, { useState, useRef } from 'react';
import { Heading } from '@commons';
import { useFormik } from 'formik';
import DOMPurify from 'dompurify';
import axios from 'axios';
import FormData from 'form-data';
import * as styles from './Newsletter.module.scss';
import './Newsletter.scss';

const Newsletter = () => {
  // HANDLE INPUT CHANGE AFTER GATSBY FUNC/SERVER SIDE VALIDATION ERROR
  const [generalErr, setGeneralErr] = useState(false);
  const [serverCalled, setServerCalled] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [generalBackendErr, setGeneralBackendErr] = useState(false);

  const GeneralBackendErr = useRef();

  // VAR FOR SERVER SIDE INPUT VALIDATION ERROR
  const serverErr = {
    email: null,
    privacyPolicy: false,
  };

  // HANDLE ON FOCUS
  function handleOnFocus() {
    if (submitSuccess) {
      setSubmitSuccess(false);
    }
  }
  const handlePostServerChange = (e) => {
    const uName = e.target.name;
    if (uName !== 'privacyPolicy') {
      serverErr.uName = null;
    }
    serverErr.uName = false;
    setGeneralErr(false);
    setSubmitSuccess(false);
    setGeneralBackendErr(false);
  };

  // A custom validation function. This must return an object
  // which keys are symmetrical to our values/initialValues
  const validate = (values) => {
    const errors = {};

    if (!values.email) {
      errors.email = 'An Email address is required';
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
      errors.email = 'Invalid email address';
    }

    if (!values.privacyPolicy) {
      errors.privacyPolicy = 'Please confirm you have read our Privacy Policy';
    }

    return errors;
  };

  // FORMIK METHOD TO HANDLE NFORM
  const formik = useFormik({
    // SETTING INITIAL FORM VALUES
    initialValues: {
      email: '',
      privacyPolicy: false,
    },

    // FORM VALIDATION
    validate,

    // SUBMIT HANDLER
    onSubmit: async (values) => {
      if (isSubmitting) {
        return;
      }
      try {
        setIsSubmitting(true);

        const formData = new FormData();

        // APPENDING TRIMMED AND SANITIZED CODE TO FORMDATA OBJECT
        formData.append('email', DOMPurify.sanitize(values.email).trim());
        formData.append('privacyPolicy', values.privacyPolicy);

        // CALLING GATSBY FUNCTION AND GETTING SERVER RESPONSE
        const url = '/api/newsletter';

        const response = await axios.post(url, formData).then((res) => res);

        if (response) {
          setServerCalled(true);
          setIsSubmitting(false);

          if (response.data.emailErr) {
            serverErr.email = response.data.emailErr;
            setGeneralErr(true);
            return;
          }

          if (response.data.privacyPolicyErr) {
            serverErr.privacyPolicy = response.data.privacyPolicyErr;
            setGeneralErr(true);
            return;
          }

          if (response.data.message) {
            if (response.data.status === 404) {
              GeneralBackendErr.current.querySelector('> div').innerHTML = response.data.message;
              setGeneralBackendErr(true);
              return;
            }

            if (response.data.status === 409) {
              GeneralBackendErr.current.querySelector('> div').innerHTML = response.data.message;
              setGeneralBackendErr(true);
              return;
            }
          }

          if (response.status === 200) {
            setSubmitSuccess(true);
            formik.resetForm();
          }
        }
      } catch (error) {
        // CATCH FOR GATSBY FUNCTION API POST CALL
        console.error(error);
      }
    }, // ONSUBMIT ENDS
  }); // useFormik ENDS

  return (
    <section
      className={`d-flex justify-content-center align-items-center bg-altdark text-white ${styles.news_letter}`}
    >
      <div className='container'>
        <div className='row align-items-center justify-content-between'>
          <Heading secondary='Our' primary='Newsletter' color='white' />

          <div className='col-md-6'>
            <p className={styles.text}>
              Subscribe to our monthly newsletter to keep to up to date on our programs and
              activities, learn about exciting developments in Africa, and discover insightful
              stories from our continent&apos;s history.
            </p>
          </div>

          <div className={`col-md-6 mt-4 mt-md-0 ${styles.editPadding_md}`}>
            <h6 className='me-auto mb-0'>Please subscribe below</h6>
            <form
              id='newsletterForm'
              name='newsletterForm'
              onSubmit={formik.handleSubmit}
              className='d-flex flex-column align-items-center justify-content-between mt-1'
            >
              {/* email */}
              <div className='form-group mt-4 d-flex flex-column'>
                <input
                  id='email'
                  name='email'
                  type='email'
                  className={`form-control rounded-pill border-0 ${styles.input}`}
                  onChange={(e) => {
                    if (serverCalled) {
                      handlePostServerChange(e);
                    }
                    formik.handleChange(e);
                  }}
                  onFocus={handleOnFocus}
                  value={formik.values.email}
                  placeholder='Type your email address'
                  aria-label="Subscriber's email"
                  aria-describedby='button-addon'
                />
                <button
                  className={`btn btn-primary text-white rounded-pill mb-3 ${styles.button}`}
                  type='submit'
                  id='button-addon'
                >
                  <span>Subscribe</span>
                  <i className='fab fa-telegram-plane ms-2' />
                </button>
              </div>
              {serverErr.email ? (
                <div className='newsletter-form-error'>{serverErr.email}</div>
              ) : null}
              {formik.errors.email ? (
                <div className='newsletter-form-error'>{formik.errors.email}</div>
              ) : null}

              {/* PRIVACY POLICY CHECKBOX */}
              <div className='form-group consent w-100 mt-3'>
                <input
                  type='checkbox'
                  onChange={(e) => {
                    if (serverCalled) {
                      handlePostServerChange(e);
                    }
                    formik.handleChange(e);
                  }}
                  name='privacyPolicy'
                  id='privacyPolicy'
                  checked={formik.values.privacyPolicy}
                  required
                />

                <span>
                  I have read and understood The Africa I Know&nbsp;
                  <a href='/privacypolicy' target='_blank'>
                    <span className='policy-link'>Privacy Policy</span>
                  </a>
                </span>
              </div>
              {serverErr.privacyPolicy ? (
                <div className='newsletter-form-error'>{serverErr.privacyPolicy}</div>
              ) : null}
              {formik.errors.privacyPolicy ? (
                <div className='newsletter-form-error'>{formik.errors.privacyPolicy}</div>
              ) : null}

              {isSubmitting && (
                <div className='is-submitting mt-2'>
                  <span>...</span>&nbsp;Submitting. Thank you for your patience.{' '}
                </div>
              )}
              {generalErr && (
                <div className='generalError mt-3'>
                  <span>Submission issues</span> <br />{' '}
                  <div>Please fix the errors in the form before submitting</div>
                </div>
              )}
              {submitSuccess && (
                <div
                  className='submitSuccess mt-4 d-flex flex-column align-items-start justify-content-center pb-3 pt-3 w-100'
                  style={{ maxWidth: '65ch' }}
                >
                  <h6 className='mb-2' style={{ width: '100%', textAlign: 'left' }}>
                    {' '}
                    Message submitted!
                  </h6>
                  <span>Thank you for subscribing to our Newsletter!</span>
                </div>
              )}
              {generalBackendErr && (
                <div className='generalBackendErr mt-3' ref={GeneralBackendErr}>
                  <span>Submission issues</span> <br />{' '}
                  <div>Please fix the errors in the form before submitting</div>
                </div>
              )}
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Newsletter;
