import React from 'react';
import Banner from './Banner';
import Newsletter from './Newsletter';

const Layout = ({
  children,
  banner,
  dynamicBanner,
  bannerStyle,
  bannerChildren,
  heroBackgroundColor,
  hide,
}) => (
  <>
    <Banner
      banner={banner}
      dynamicBanner={dynamicBanner}
      style={bannerStyle}
      hide={hide}
      heroBackgroundColor={heroBackgroundColor}
    >
      {bannerChildren}
    </Banner>

    <article id='index-article'>
      {children}
      <Newsletter />
    </article>
  </>
);

export default Layout;
