import React from 'react';

// import Header from './Header';
import * as styles from './Banner.module.scss';

import backgroundImage from '../assets/images/background-image.jpg';

const Banner = ({
  banner = backgroundImage,
  dynamicBanner,
  children,
  style,
  heroBackgroundColor,
  hide,
}) => (
  <div className={styles.banner} style={{ backgroundImage: `url(${banner})`, ...style }}>
    <section
      className={`section-padding position-relative bg-${heroBackgroundColor}`}
      style={{
        backgroundImage: dynamicBanner
          ? `linear-gradient(0deg, rgba(6, 25, 1, 0.45), rgba(6, 25, 1, 0.45)), url(${dynamicBanner})`
          : '',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
        display: hide ? 'none' : 'block',
      }}
    >
      {children}
    </section>
  </div>
);

export default Banner;
